"use client";

import { useState, useRef, useEffect } from "react";
import { usePathname } from "next/navigation";
import { Camera, Upload, SendHorizonal, HelpCircle } from "lucide-react";

import { AppSidebar } from "@/components/app-sidebar";
import {
  SidebarProvider,
  SidebarInset,
  SidebarTrigger,
} from "@/components/ui/sidebar";
import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  BreadcrumbList,
  BreadcrumbPage,
  BreadcrumbSeparator,
} from "@/components/ui/breadcrumb";
import { Separator } from "@/components/ui/separator";
import {
  InputGroup,
  InputGroupAddon,
  InputGroupButton,
  InputGroupTextarea,
  InputGroupText,
} from "@/components/ui/input-group";
import {
  Tooltip,
  TooltipTrigger,
  TooltipContent,
} from "@/components/ui/tooltip";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { DropdownMenuLabel } from "@radix-ui/react-dropdown-menu";
import { Message, useChatStore } from "@/store/useChatStore";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { useUserStore } from "@/store/useUserStore";
import ChatArea from "./chatArea";
import { usePromptStore } from "@/store/useUserPrompt";

type ChatMode = "Auto" | "Conversation" | "Syllabus" | "Quiz";

export default function Page() {
  const pathname = usePathname();
  const chatName = decodeURIComponent(pathname.replace(/^\/chat\/?/, "")); // cleaner extraction
  const { prompt } = usePromptStore();

  const [input, setInput] = useState(prompt);
  const [tokenUsage, setTokenUsage] = useState(0);

  // This is the correct frontend call to match the backend fix
  const {
    currentChat,
    setCurrentChat,
    addMessage,
    setChatSessions,
    chatSessions,
    setAssistantTyping,
    setAssistantMessageContent,
    setCurrentChatMessages,
  } = useChatStore();

  // This function is correct as-is

  const handleSend = async () => {
    if (!input.trim()) return;

    const userMessage: Message = { role: "user", content: input };
    const messageToSend = input;

    // ---  FIX PART 1: Optimistic UI Update  ---
    // Store the original chat state *before* we make changes
    const originalCurrentChat = currentChat;
    const isNewChat = !originalCurrentChat;

    let sessionToSend: string | null = null; // This is what we'll send to the API
    let idToUpdate: number; // This is the ID we'll use for the store

    if (isNewChat) {
      // 1a. It's a new chat. Create a temporary session *immediately*.
      const tempId = `temp_${Date.now()}`;
      const newSessionData = {
        id: Number(tempId), // Use a temporary ID
        title: messageToSend.substring(0, 30) + "...",
        actor: "teacher_agent",
        time: new Date().toISOString(),
        messages: [userMessage], //  Add *only* the user's message
      };

      // Update the store. This makes the user's message appear instantly.
      setChatSessions([...chatSessions, newSessionData]);
      setCurrentChat(newSessionData);

      sessionToSend = null; // Tell the backend to create a new session
      idToUpdate = Number(tempId); // This is the temporary ID we need to update later
    } else {
      // 1b. It's an existing chat. This logic is simpler.
      addMessage(originalCurrentChat.id, userMessage);
      sessionToSend = String(originalCurrentChat.id);
      idToUpdate = originalCurrentChat.id;
    }
    // --- End of Fix Part 1 ---

    setInput("");
    setTokenUsage((prev) => prev + Math.ceil(messageToSend.length / 4)); // 2. Set assistant to 'typing'

    setAssistantTyping(true);
    setAssistantMessageContent("");

    try {
      // 3. Send request
      const response = await fetch("http://127.0.0.1:8000/api/chat", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          user_id: "1",
          message: messageToSend,
          session_id: sessionToSend, //  Sends `null` for new chats, or the ID for existing
        }),
      });

      if (!response.ok) throw new Error("Agent response failed.");

      const data = await response.json();
      const finalAgentContent = data.response.trim();
      const realSessionId = data.session_id; //  Get the *real* ID from the backend // --- Your regex and matching (this part is correct) ---

      const quizRegex = /\[QuizID: ([^\]]+)\]/;
      const syllabusRegex = /\[SyllabusID:([^\]]+)\]/;
      const videoRegex = /(https?:\/\/[^\s]+\.mp4)/;

      const quizMatch = finalAgentContent.match(quizRegex);
      const syllabusMatch = finalAgentContent.match(syllabusRegex);
      const videoMatch = finalAgentContent.match(videoRegex); // --- Also update local state (as you were doing) ---

      const agentMessage: Message = {
        role: "assistant",
        content: finalAgentContent,
        quiz: (quizMatch && quizMatch[1]) || null,
        syllabus: (syllabusMatch && syllabusMatch[1]) || null,
        video: (videoMatch && videoMatch[1]) || null,
      }; // ***************************************** // 4. Update store state
      setAssistantTyping(false);
      setAssistantMessageContent(finalAgentContent);

      // ---  FIX PART 2: Update Store with Server Data  ---
      if (isNewChat) {
        // 5a. It *was* a new chat. We need to update our temporary session.

        // Update the *current* chat in the store
        setCurrentChat((prevChat) => ({
          ...prevChat!,
          id: realSessionId, //  Update the ID from temp to real
          messages: [...prevChat!.messages, agentMessage], //  Add the assistant message
        }));

        // Update the session in the *sidebar list*
        setChatSessions((prevSessions) =>
          prevSessions.map((session) =>
            session.id === idToUpdate // Find the one with the `temp_...` ID
              ? {
                  ...session,
                  id: realSessionId,
                  messages: [userMessage, agentMessage],
                } // Update its ID and messages
              : session
          )
        );
      } else {
        // 5b. It was an existing chat. Just add the new message.
        if (agentMessage.content.length > 0) {
          addMessage(idToUpdate, agentMessage); // idToUpdate is just originalCurrentChat.id
        } else {
          addMessage(idToUpdate, {
            role: "assistant",
            content: "Li: Miniwave b vn  ri, hy qua li sau",
          });
        }
      }
      // --- End of Fix Part 2 ---
    } catch (error) {
      console.error("Error sending message:", error);

      setAssistantTyping(false);
      setAssistantMessageContent("");

      // ---  FIX PART 3: Error Handling  ---
      // This will now correctly add the error message to both
      // new (temporary) chats and existing chats.
      const errorMessage: Message = {
        role: "assistant",
        content: "Li: Khng th kt ni vi Miniwave.",
      };

      // `idToUpdate` is *always* the correct ID (temp or real)
      addMessage(idToUpdate, errorMessage);
      // --- End of Fix Part 3 ---
    }
  };
  useEffect(() => {
    async function fetchSessionEvents(
      user_id: string,
      session_id: string | undefined
    ) {
      if (!session_id) return [];
      const res = await fetch(
        `http://127.0.0.1:8000/api/sessions/${user_id}/${session_id}`
      );

      if (!res.ok) {
        console.error(
          "Failed to fetch session events:",
          res.status,
          res.statusText
        );
        return [];
      }

      const json = await res.json();
      const events = json.events || [];
      console.log("Fetched raw events:", json);

      // ********  NEW LOGIC START  ********
      // Define the regex patterns
      const quizRegex = /\[QuizID: ([^\]]+)\]/;
      const syllabusRegex = /\[SyllabusID:([^\]]+)\]/;
      const videoRegex = /(https?:\/\/[^\s]+\.mp4)/;

      // Map over the events and parse the content
      const parsedEvents = events.map((message: Message) => {
        // Only parse assistant messages that have content
        if (message.role === "assistant" && message.content) {
          const quizMatch = message.content.match(quizRegex);
          const syllabusMatch = message.content.match(syllabusRegex);
          const videoMatch = message.content.match(videoRegex);

          // Return a new message object with the parsed data
          return {
            ...message,
            quiz: (quizMatch && quizMatch[1]) || null,
            syllabus: (syllabusMatch && syllabusMatch[1]) || null,
            video: (videoMatch && videoMatch[1]) || null,
          };
        }
        // Return user messages or empty messages as-is
        return message;
      });

      // Return the newly parsed array
      return parsedEvents;
      // ********  NEW LOGIC END  ********
    }

    // Guard clause: (This is correct)
    if (
      !currentChat ||
      (currentChat.messages && currentChat.messages.length > 0)
    ) {
      return;
    }

    const session_id = String(currentChat.id);

    const loadMessages = async () => {
      try {
        // 'events' will now be the *parsed* array from fetchSessionEvents
        const events = await fetchSessionEvents("1", session_id);

        // This now saves the messages *with* the quiz/syllabus/video data
        setCurrentChatMessages(currentChat.id, events);
        console.log("Saved parsed events to store:", events);
      } catch (err) {
        console.error("Failed to fetch sessions:", err);
      }
    };

    loadMessages();
  }, [currentChat, setCurrentChatMessages]);

  // --- UI ---
  return (
    <SidebarProvider
      style={{ "--sidebar-width": "350px" } as React.CSSProperties}
    >
      <AppSidebar />
      <SidebarInset>
        {/* Header */}
        <header className="bg-background sticky top-0 flex items-center w-full gap-2 border-b p-4">
          <SidebarTrigger className="-ml-1" />
          <Separator orientation="vertical" className="mr-2 h-2" />
          <Breadcrumb>
            <BreadcrumbList>
              <BreadcrumbItem className="hidden md:block">
                <BreadcrumbLink href="/chat">Chat Wave</BreadcrumbLink>
              </BreadcrumbItem>
              {chatName && (
                <>
                  <BreadcrumbSeparator className="hidden md:block" />
                  <BreadcrumbItem>
                    <BreadcrumbPage>{chatName}</BreadcrumbPage>
                  </BreadcrumbItem>
                </>
              )}
            </BreadcrumbList>
          </Breadcrumb>
          <div className="absolute right-5">
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button
                  variant="ghost"
                  size="sm"
                  className="text-[#F66868] hover:bg-[#F66868]/10 hover:text-[#F66868]/80 cursor-pointer transition-all duration-300"
                >
                  <HelpCircle />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent
                align="end"
                className="p-3 text-sm leading-relaxed max-w-[240px]"
              >
                <DropdownMenuLabel className="font-semibold text-center mb-2">
                  Chat Wave l g?
                </DropdownMenuLabel>
                <p>
                   Ti y, bn c th tr chuyn vi Miniwave bng ngn ng
                  k hiu, luyn tp qua hi thoi, to quiz vui nhn v hc
                  theo gio trnh ring.
                </p>
                <p className="mt-2 text-gray-500 text-xs">
                  Hy bt u g tin nhn  Miniwave cng bn hc nh!
                </p>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </header>

        {/* Chat Hero Section */}
        <div className="flex-1 overflow-y-auto p-4">
          <div className="flex-1 overflow-y-auto p-4 flex items-center justify-center h-full">
            {currentChat ? (
              <ChatArea messages={currentChat.messages} />
            ) : (
              <div className="flex flex-col items-center">
                <Image
                  src="/capybara.svg"
                  alt="hero_section_chat"
                  width={280} //  smaller size
                  height={280}
                  className="select-none item-justify-center my-auto"
                />
                <div className="text-center mt-4 text-gray-500">
                  <h2 className="text-4xl font-semibold mb-2 text-black">
                    Cho mng n vi{" "}
                    <span className="text-[#F66868] font-bold">Chat Wave</span>
                  </h2>
                  <p className="mb-4 text-lg">
                    Chn mt cuc tr chuyn hoc to cuc tr chuyn mi cng{" "}
                    <b>Miniwave</b>
                  </p>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Chat Input Section */}
        <div className="sticky bottom-0 left-0 right-0 p-4 z-50">
          <div className="max-w-3xl w-full mx-auto mb-[20px]">
            <InputGroup>
              <InputGroupTextarea
                placeholder="Bt u cuc tr chuyn vi Miniwave..."
                value={input}
                onChange={(e) => setInput(e.target.value)}
                rows={3}
                className="resize-none"
              />

              <InputGroupAddon
                align="block-end"
                className="flex items-center gap-2"
              >
                {/* Token Count */}
                <InputGroupText className="ml-auto text-sm text-gray-500">
                  {tokenUsage} tokens  dng
                </InputGroupText>

                <Separator orientation="vertical" className="!h-6" />

                {/* Send */}
                <Tooltip>
                  <TooltipTrigger asChild>
                    <InputGroupButton
                      variant="default"
                      className="rounded-full p-2 flex items-center justify-center bg-[#F66868]"
                      onClick={handleSend}
                      disabled={!input.trim()}
                    >
                      Gi
                      <SendHorizonal className="ml-1 w-5 h-5" />
                    </InputGroupButton>
                  </TooltipTrigger>
                  <TooltipContent>Gi yu cu</TooltipContent>
                </Tooltip>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </div>
      </SidebarInset>
    </SidebarProvider>
  );
}
