// store/chatStore.ts
import { create } from "zustand";

export type Message = {
  role: "user" | "assistant";
  content: string;
  video?: string | null;
  quiz?: number | null;
  syllabus?: number | null;
};

export interface ChatSession {
  id: number;
  title: string;
  actor: string;
  time: string;
  messages: Message[];
}

interface ChatState {
  chatSessions: ChatSession[];
  currentChat?: ChatSession | null;
  assistantTyping: boolean;
  assistantMessageContent: string;

  setChatSessions: (sessions: ChatSession[] | undefined | null) => void;
  setCurrentChat: (chat: ChatSession | null) => void;
  setCurrentChatMessages: (chatId: number, messages: Message[]) => void;
  addMessage: (chatId: number, message: Message) => void;
  setAssistantTyping: (isTyping: boolean) => void;
  setAssistantMessageContent: (content: string) => void;
}

export const useChatStore = create<ChatState>((set) => ({
  chatSessions: [],
  currentChat: null,
  assistantTyping: false,
  assistantMessageContent: "",

  //  FIXED: Always ensure sessions is an array
  setChatSessions: (sessions) =>
    set({
      chatSessions: Array.isArray(sessions) ? sessions : [],
    }),

  setCurrentChat: (chat) => set({ currentChat: chat }),

  //  FIXED: Added safety check for chatSessions
  addMessage: (chatId, message) =>
    set((state) => {
      const sessions = Array.isArray(state.chatSessions)
        ? state.chatSessions
        : [];

      return {
        chatSessions: sessions.map((chat) =>
          chat.id === chatId
            ? {
                ...chat,
                messages: [...(chat.messages || []), message],
              }
            : chat
        ),
        currentChat:
          state.currentChat?.id === chatId
            ? {
                ...state.currentChat,
                messages: [...(state.currentChat.messages || []), message],
              }
            : state.currentChat,
      };
    }),

  setAssistantTyping: (isTyping) => set({ assistantTyping: isTyping }),

  setAssistantMessageContent: (content) =>
    set({ assistantMessageContent: content }),

  //  FIXED: Added safety check for chatSessions
  setCurrentChatMessages: (chatId, messages) =>
    set((state) => {
      const sessions = Array.isArray(state.chatSessions)
        ? state.chatSessions
        : [];

      return {
        chatSessions: sessions.map((chat) =>
          chat.id === chatId
            ? {
                ...chat,
                messages: messages,
              }
            : chat
        ),
        currentChat:
          state.currentChat?.id === chatId
            ? {
                ...state.currentChat,
                messages: messages,
              }
            : state.currentChat,
      };
    }),
}));
