"use client";

import * as React from "react";
import {
  BookOpen,
  Database,
  Globe2,
  Home,
  MessageSquareCodeIcon,
  PlusCircle,
  Search,
  X,
} from "lucide-react";

import { NavUser } from "@/components/nav-user";
import {
  Sidebar,
  SidebarContent,
  SidebarFooter,
  SidebarGroup,
  SidebarGroupContent,
  SidebarHeader,
  SidebarInput,
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
  useSidebar,
} from "@/components/ui/sidebar";
import { Button } from "./ui/button";
import { useRouter } from "next/navigation";
import Image from "next/image";
import { ChatSession, useChatStore } from "@/store/useChatStore";
import { formatActor, formatTime } from "@/lib/utils";

//  Types for better type safety
interface NavItem {
  title: string;
  url: string;
  icon: React.ComponentType<{ color?: string }>;
  isActive: boolean;
}

//  Constants
const APP_CONFIG = {
  appName: "Wave Wave",
  companyName: "Acme Inc",
  companyType: "Enterprise",
  logoPath: "logo.svg",
  apiBaseUrl: process.env.NEXT_PUBLIC_API_URL || "http://127.0.0.1:8000",
  defaultUserId: "1",
  maxTitleLength: 30,
  truncateOffset: 4,
} as const;

const NAVIGATION_ITEMS: NavItem[] = [
  { title: "Trang ch", url: "/", icon: Home, isActive: true },
  { title: "Hc tp", url: "/study", icon: BookOpen, isActive: false },
  { title: "Cng ng", url: "/community", icon: Globe2, isActive: false },
  { title: "T in", url: "/dictionary", icon: Database, isActive: false },
  {
    title: "Chat Wave",
    url: "/chat",
    icon: MessageSquareCodeIcon,
    isActive: false,
  },
];

export function AppSidebar({ ...props }: React.ComponentProps<typeof Sidebar>) {
  //  State management
  const [activeItem, setActiveItem] = React.useState<NavItem>(
    NAVIGATION_ITEMS[0]
  );
  const [searchQuery, setSearchQuery] = React.useState("");
  const [isLoadingSessions, setIsLoadingSessions] = React.useState(false);
  const [error, setError] = React.useState<string | null>(null);

  const { chatSessions, setChatSessions, currentChat, setCurrentChat } =
    useChatStore();

  //  Ensure chatSessions is always an array
  const safeChatSessions = React.useMemo(() => {
    return Array.isArray(chatSessions) ? chatSessions : [];
  }, [chatSessions]);
  const { setOpen } = useSidebar();
  const router = useRouter();

  //  Debounced search for better performance
  const debouncedSearchQuery = useDebounce(searchQuery, 300);

  //  Fetch sessions with error handling
  const fetchUserSessions = React.useCallback(
    async (userId: string): Promise<ChatSession[]> => {
      try {
        const res = await fetch(
          `${APP_CONFIG.apiBaseUrl}/api/sessions/${userId}`,
          {
            headers: {
              "Content-Type": "application/json",
            },
          }
        );

        if (!res.ok) {
          throw new Error(`HTTP error! status: ${res.status}`);
        }

        const json = await res.json();
        return json.sessions || [];
      } catch (err) {
        console.error("Failed to fetch sessions:", err);
        throw err;
      }
    },
    []
  );

  //  Load sessions on mount
  React.useEffect(() => {
    let isMounted = true;

    (async () => {
      setIsLoadingSessions(true);
      setError(null);

      try {
        const sessions = await fetchUserSessions(APP_CONFIG.defaultUserId);
        if (isMounted) {
          setChatSessions(sessions);
        }
      } catch (err) {
        if (isMounted) {
          setError("Khng th ti cuc tr chuyn. Vui lng th li.");
          setChatSessions([]);
        }
      } finally {
        if (isMounted) {
          setIsLoadingSessions(false);
        }
      }
    })();

    return () => {
      isMounted = false;
    };
  }, [setChatSessions, fetchUserSessions]);

  //  Memoized filtered sessions with debounced search
  const filteredSessions = React.useMemo(() => {
    if (!debouncedSearchQuery.trim()) return safeChatSessions;

    const lower = debouncedSearchQuery.toLowerCase().trim();
    return safeChatSessions.filter((session) =>
      session.title.toLowerCase().includes(lower)
    );
  }, [safeChatSessions, debouncedSearchQuery]);

  //  Handle navigation click
  const handleNavClick = React.useCallback(
    (item: NavItem) => {
      setActiveItem(item);

      if (item.title === "Chat Wave") {
        setOpen(true);
      } else {
        router.push(item.url);
      }
    },
    [router, setOpen]
  );

  //  Handle new chat creation
  const handleNewChat = React.useCallback(() => {
    setCurrentChat(null);
    setSearchQuery(""); // Clear search when creating new chat
  }, [setCurrentChat]);

  //  Handle chat selection
  const handleChatSelect = React.useCallback(
    (chat: ChatSession) => {
      setCurrentChat(chat);
    },
    [setCurrentChat]
  );

  //  Clear search
  const handleClearSearch = React.useCallback(() => {
    setSearchQuery("");
  }, []);

  //  Truncate title helper
  const truncateTitle = (title: string): string => {
    if (title.length <= APP_CONFIG.maxTitleLength) return title;
    return `${title.slice(
      0,
      APP_CONFIG.maxTitleLength - APP_CONFIG.truncateOffset
    )}...`;
  };

  return (
    <Sidebar
      collapsible="icon"
      className="overflow-hidden *:data-[sidebar=sidebar]:flex-row"
      {...props}
    >
      {/* ------------------------------------
          Sidebar 1  Navigation Icons
      ------------------------------------ */}
      <Sidebar
        collapsible="none"
        className="w-[calc(var(--sidebar-width-icon)+1px)] border-r"
      >
        <SidebarHeader>
          <SidebarMenu>
            <SidebarMenuItem>
              <SidebarMenuButton
                tooltip={{ children: APP_CONFIG.appName, hidden: false }}
                size="lg"
                asChild
                className="md:h-8 md:p-0"
              >
                <a href="/">
                  <div className="bg-[#F66868]/20 flex size-8 items-center justify-center rounded-lg">
                    <Image
                      src={APP_CONFIG.logoPath}
                      alt={`${APP_CONFIG.appName} logo`}
                      width={24}
                      height={24}
                      priority
                    />
                  </div>
                  <div className="grid flex-1 text-left text-sm leading-tight">
                    <span className="truncate font-medium">
                      {APP_CONFIG.companyName}
                    </span>
                    <span className="truncate text-xs">
                      {APP_CONFIG.companyType}
                    </span>
                  </div>
                </a>
              </SidebarMenuButton>
            </SidebarMenuItem>
          </SidebarMenu>
        </SidebarHeader>

        <SidebarContent>
          <SidebarGroup>
            <SidebarGroupContent className="px-1.5 md:px-0">
              <SidebarMenu>
                {NAVIGATION_ITEMS.map((item) => (
                  <SidebarMenuItem key={item.title}>
                    <SidebarMenuButton
                      tooltip={{ children: item.title, hidden: false }}
                      onClick={() => handleNavClick(item)}
                      isActive={activeItem?.title === item.title}
                      className="px-2.5 md:px-2 cursor-pointer"
                    >
                      <item.icon color="#F66868" />
                      <span>{item.title}</span>
                    </SidebarMenuButton>
                  </SidebarMenuItem>
                ))}
              </SidebarMenu>
            </SidebarGroupContent>
          </SidebarGroup>
        </SidebarContent>
      </Sidebar>

      {/* ------------------------------------
          Sidebar 2  Chat Sessions
      ------------------------------------ */}
      <Sidebar collapsible="none" className="hidden flex-1 md:flex gap-0">
        <SidebarHeader className="gap-3.5 border-b p-4">
          <div className="flex w-full items-center justify-between">
            <Button
              variant="default"
              size="sm"
              onClick={handleNewChat}
              className="gap-2"
            >
              <PlusCircle className="h-4 w-4" />
              To chat mi
            </Button>
          </div>

          {/*  Enhanced search box with clear button */}
          <div className="relative">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
            <SidebarInput
              placeholder="Tm kim cuc tr chuyn..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="pl-9 pr-9"
            />
            {searchQuery && (
              <button
                onClick={handleClearSearch}
                className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600 transition-colors"
                aria-label="Clear search"
              >
                <X className="h-4 w-4" />
              </button>
            )}
          </div>
        </SidebarHeader>

        <SidebarContent>
          <SidebarGroup className="px-0 p-0">
            <SidebarGroupContent className="max-h-[calc(100vh-200px)] overflow-y-auto scrollbar-thin scrollbar-thumb-pink-300 scrollbar-track-transparent">
              {/*  Loading state */}
              {isLoadingSessions ? (
                <div className="p-4 text-sm text-gray-500 text-center">
                  <div className="animate-pulse">ang ti...</div>
                </div>
              ) : error ? (
                /*  Error state */
                <div className="p-4 text-sm text-red-500 text-center">
                  {error}
                </div>
              ) : filteredSessions.length === 0 ? (
                /*  Empty state with context */
                <div className="p-4 text-sm text-gray-500 text-center">
                  {searchQuery
                    ? "Khng tm thy cuc tr chuyn no"
                    : "Cha c cuc tr chuyn no"}
                </div>
              ) : (
                /*  Chat sessions list */
                filteredSessions.map((chat) => (
                  <div
                    key={chat.id}
                    className={`flex flex-col gap-1 p-3 border-b border-pink-200 transition-all cursor-pointer hover:bg-pink-100 ${
                      currentChat?.id === chat.id
                        ? "bg-[#f66868]/40"
                        : "bg-[#F66868]/5"
                    }`}
                    onClick={() => handleChatSelect(chat)}
                    role="button"
                    tabIndex={0}
                    onKeyDown={(e) => {
                      if (e.key === "Enter" || e.key === " ") {
                        handleChatSelect(chat);
                      }
                    }}
                  >
                    <div className="flex items-center justify-between gap-2">
                      <span className="font-semibold text-[#C73B3B] truncate flex-1">
                        {truncateTitle(chat.title)}
                      </span>
                      <span className="text-xs text-gray-500 whitespace-nowrap">
                        {formatTime(chat.time)}
                      </span>
                    </div>
                    <p className="text-xs text-gray-600 line-clamp-1">
                      {formatActor(chat.actor)}
                    </p>
                  </div>
                ))
              )}
            </SidebarGroupContent>
          </SidebarGroup>
        </SidebarContent>
      </Sidebar>
    </Sidebar>
  );
}

//  Custom debounce hook for search optimization
function useDebounce<T>(value: T, delay: number): T {
  const [debouncedValue, setDebouncedValue] = React.useState<T>(value);

  React.useEffect(() => {
    const handler = setTimeout(() => {
      setDebouncedValue(value);
    }, delay);

    return () => {
      clearTimeout(handler);
    };
  }, [value, delay]);

  return debouncedValue;
}
